<template>
  <v-form @submit.prevent="save" ref="form" v-model="valid" lazy-validation>
    <v-text-field
      label="이메일"
      v-model="form.UserEmail"
      :rules="rules.email()"
    />
    <input-password
      label="비밀번호"
      v-model="form.UserPwd"
      :rules="rules.password()"
    />
    <v-btn
      block
      color="primary"
      type="submit"
      :loading="isLoading"
      :click="login"
    >
      로그인
    </v-btn>
  </v-form>
</template>

<script>
import validateRules from "../util/validateRules";
import InputPassword from "./InputPassword.vue";
// import axios from "axios";

export default {
  components: { InputPassword },
  name: "SignInForm",
  props: {
    isLoading: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      valid: true,
      form: {
        UserEmail: "",
        UserPwd: "",
      },
    };
  },
  computed: {
    rules: () => validateRules,
  },
  methods: {
    async save() {
      this.$refs.form.validate();
      await this.$nextTick();
      if (!this.valid) return;
      this.$emit("save", this.form);
    },
    login: function (event) /*eslint-disable-line no-unused-vars*/ {
      this.$http
        .post("/api/users/login", {
          user: this.user,
        })
        .then(
          (res) => {
            //로그인 성공
            alert(res.data.message);
          },
          (err) => {
            // error 를 보여줌
            alert("Login failed! please check your id or password" + err);
          }
        )
        .catch((err) => {
          alert(err);
        });
    },
  },
};
</script>
